<template>

    <ValidationObserver ref="observer" v-slot="{ handleSubmit }">
        <b-modal id="modalEditPeoples" ref="modalEditPeoples" hide-footer title="Edit People" >
        
            <b-container fluid>
                <!-- <b-form @submit="onSubmit" @reset="onReset"> -->
                <b-form @reset="onReset" @submit.stop.prevent="handleSubmit(onSubmit)">
                    <b-row>
                        <b-col>
                            <validation-provider
                                name="Last Name"
                                :rules="{ required: true, min: 3 }"
                                v-slot="validationContext"
                            >
                                <b-form-group id="input-group-lastname-people-edit" label="Last Name" label-for="input-lastname-people-edit">
                                    <b-form-input
                                        id="input-people-lastname"
                                        ref="input-people-lastname"
                                        name="input-lastname-people-edit"
                                        v-model="people.last_name"
                                        placeholder="Last name"
                                        maxlength="50"
                                        :state="getValidationState(validationContext)"
                                        aria-describedby="input-1-live-feedback"
                                        trim
                                        autofocus
                                    ></b-form-input>
                                    <b-form-invalid-feedback id="input-1-live-feedback">{{ validationContext.errors[0] }}</b-form-invalid-feedback>
                                </b-form-group>
                            </validation-provider>
                        </b-col>
                    </b-row>
                    <b-row>
                        <b-col>
                            <validation-provider
                                name="First Name"
                                :rules="{ required: true, min: 3 }"
                                v-slot="validationContext"
                            >
                                <b-form-group id="input-group-firstname-people-edit" label="First Name" label-for="input-firstname-people-edit">
                                    <b-form-input
                                        id="input-people-firstname"
                                        ref="input-people-firstname"
                                        name="input-firstname-people-edit"
                                        v-model="people.first_name"
                                        placeholder="First name"
                                        maxlength="50"
                                        :state="getValidationState(validationContext)"
                                        aria-describedby="input-1-live-feedback"
                                        trim
                                    ></b-form-input>
                                    <b-form-invalid-feedback id="input-1-live-feedback">{{ validationContext.errors[0] }}</b-form-invalid-feedback>
                                </b-form-group>
                            </validation-provider>
                        </b-col>
                    </b-row>
                    <b-row>
                        <b-col>
                            <b-form-group id="input-group-middlename-people-edit" label="Middle Name" label-for="input-middlename-people-edit">
                                <b-form-input
                                    id="input-people-middlename"
                                    ref="input-people-middlename"
                                    name="input-middlename-people-edit"
                                    v-model="people.middle_name"
                                    placeholder="Middle name of Person"
                                    maxlength="50"
                                    aria-describedby="input-1-live-feedback"
                                    trim
                                ></b-form-input>    
                            </b-form-group>
                        </b-col>
                    </b-row>
                    <b-row>
                        <b-col>
                            <validation-provider
                                name="Email"
                                :rules="{ required: true, min: 3 }"
                                v-slot="validationContext"
                            >
                                <b-form-group id="input-group-email-people-edit" label="Email Name" label-for="input-email-people-edit">
                                    <b-form-input
                                        id="input-people-email"
                                        ref="input-people-email"
                                        name="input-email-people-edit"
                                        type="email"
                                        v-model="people.email"
                                        placeholder="Personal Email"
                                        maxlength="50"
                                        :state="getValidationState(validationContext)"
                                        aria-describedby="input-1-live-feedback"
                                        trim
                                    ></b-form-input>
                                    <b-form-invalid-feedback id="input-1-live-feedback">{{ validationContext.errors[0] }}</b-form-invalid-feedback>
                                </b-form-group>
                            </validation-provider>
                        </b-col>
                    </b-row>
                    <b-row>
                        <b-col>
                            <validation-provider
                                name="Address"
                                :rules="{ required: true, min: 3 }"
                                v-slot="validationContext"
                            >
                                <b-form-group id="input-group-address-people-edit" label="Address Name" label-for="input-address-people-edit">
                                    <b-form-input
                                        id="input-people-address"
                                        ref="input-people-address"
                                        name="input-address-people-edit"
                                        v-model="people.address"
                                        placeholder="Personal Address"
                                        maxlength="50"
                                        :state="getValidationState(validationContext)"
                                        aria-describedby="input-1-live-feedback"
                                        trim
                                    ></b-form-input>
                                    <b-form-invalid-feedback id="input-1-live-feedback">{{ validationContext.errors[0] }}</b-form-invalid-feedback>
                                </b-form-group>
                            </validation-provider>
                        </b-col>
                    </b-row>
                    <b-row>
                        <b-col>
                            <b-form-group id="input-group-country-people-edit" label="Select Country" label-for="input-country-people-edit">
                                <b-form-select 
                                    v-model="people.country_id" 
                                    :options="countries"
                                    aria-describedby="input-live-help input-live-feedback"
                                    placeholder="Country">
                                </b-form-select>
                            </b-form-group>
                        </b-col>
                    </b-row>
                    <b-row>
                        <b-col>
                            <b-button variant="success" type="submit" size="sm" v-if="people.id == '0'">Save changes</b-button>
                            <b-button variant="success" type="submit" size="sm" v-if="people.id != '0'">Apply changes</b-button>
                            <b-button variant="danger" type="reset" size="sm" >Reset</b-button>
                        </b-col>
                    </b-row>
                </b-form>
            </b-container>
        
        </b-modal>
    </ValidationObserver>

</template>

<script>

    export default {
         
        name: 'ModalEditPeople',
        props: ['people', 'countries'],
        created() {

                

            this.$root.$on('bv::modal::show', (bvEvent, modalEditPeoples) => { 

               if (this.people.id != '0' && typeof this.people.id != 'undefined') {
                db.collection('people').doc(this.people.id)
                        .get()
                        .then(
                            snapshot => {
                                if (!snapshot.exists) return
                                let data = snapshot.data() 
                                this.people.last_name = data.last_name
                                this.people.first_name = data.first_name
                                this.people.middle_name = data.middle_name
                                this.people.email = data.email
                                this.people.address = data.address
                                this.people.country_id = data.country_id

                            }
                        )
               }
                
            })  
        },
        methods: {
            getValidationState({ dirty, validated, valid = null }) {
                return dirty || validated ? valid : null;
            } ,
            onSubmit() {
                if (this.people.id != '0') {
                    let show_people_id = this.people.id
                    let setDoc = 
                        db.collection("people")
                            .doc(this.people.id)
                            .set({
                                date_updated: new Date(),
                                last_name: this.people.last_name,
                                first_name: this.people.first_name,
                                middle_name: this.people.middle_name,
                                email: this.people.email,
                                address: this.people.address,
                                country_id: this.people.country_id
                            })
                            .then(function() {
                                console.log("Document written with ID - onSubmit() - SetEvent", show_people_id)
                            })
                            .catch(function(error) {
                                console.error("Error writing document - onSubmit() - SetEvent", error)
                            })
                            
                        this.$bvModal.msgBoxOk('Document was edited successfully', {
                                title: 'Confirmation',
                                size: 'sm',
                                buttonSize: 'sm',
                                okVariant: 'success',
                                headerClass: 'p-2 border-bottom-0',
                                footerClass: 'p-2 border-top-0',
                                centered: true
                                })
                                .then(value => {
                                    this.boxTwo = value
                                })
                                .catch(err => {
                                    // An error occurred
                                })
                }

                if (this.people.id == '0') {
                    db.collection('people').add({
                                date_updated: new Date(),
                                last_name: this.people.last_name,
                                first_name: this.people.first_name,
                                middle_name: this.people.middle_name,
                                email: this.people.email,
                                address:this.people.address,
                                country_id: this.people.country_id
                            }
                        )
                        .then(function(docRef) {
                                console.log("Document written with ID - addData(): ", docRef.id)
                            })
                            .catch(function(error) {
                                console.error("Error adding document - addData(): ", error)
                            }); 

                    this.$bvModal.msgBoxOk('Document was added successfully', {
                                title: 'Confirmation',
                                size: 'sm',
                                buttonSize: 'sm',
                                okVariant: 'success',
                                headerClass: 'p-2 border-bottom-0',
                                footerClass: 'p-2 border-top-0',
                                centered: true
                                })
                                .then(value => {
                                    this.boxTwo = value
                                })
                                .catch(err => {
                                    // An error occurred
                                })
                } 
                
                this.$refs['modalEditPeoples'].hide()
            },
            onReset(evt){
                evt.preventDefault()

                let focus_object = this.actual_object_focus
                this.people.last_name = ''
                this.people.first_name = ''
                this.people.middle_name = ''
                this.people.email = ''
                this.people.address = ''
                this.people.country_id = ''
            }
        }
    }
</script>